export default "/*\n    title: 刷新特别关心\n    discription: 有些框架获取分组可能不包含特别关心，\n            你可以用这个脚本来指定特别关心的用户；\n            享受特别关心的相关通知功能。\n*/\n\nconst specialList = [];\nfor (const user of this.runtimeData.userList) {\n  if (specialList.includes(user.user_id)) {\n    user.class_id = 9999;\n    user.class_name = '特别关心';\n    const classInfo = this.runtimeData.tags.classes.find(item => item.class_id == 9999);\n    if (classInfo == undefined) {\n      this.runtimeData.tags.classes.unshift({\n        class_id: 9999,\n        class_name: '特别关心'\n      });\n    }\n  }\n}";